.modal input[type=checkbox] {
    position: relative !important;
    height: 0;
    margin-right: 12px;
}

.modal input[type=checkbox]:before,
.modal input[type=checkbox]:after {
    margin-right: unset !important;
}

.modal input[type="checkbox"]:before {
    content: '';
    display: inline-block;
    position: absolute;
    right: 0;
    width: 14px;
    height: 14px;
    margin-right: 14px;
    border: 1px solid rgba(34, 26, 51, 0.2);
    border-radius: 4px;
    transition: .2s ease-in-out;
}

.modal input[type="checkbox"]:after {
    content: '';
    display: inline-block;
    position: absolute;
    top: 4px;
    right: 4px;
    width: 6px;
    height: 6px;
    margin-right: 14px;
    border-radius: 4px;
    transition: .2s ease-in-out;
}

.modal input[type="checkbox"]:checked:before {
    display: inline-block;
    position: absolute;
    width: 14px;
    height: 14px;
    margin-right: 14px;
    border: 1px solid #6531A2;
    border-radius: 4px;
}

.modal input[type="checkbox"]:checked:after {
    display: inline-block;
    position: absolute;
    width: 6px;
    height: 6px;
    border-radius: 4px;
    background: #6531A2;
}

.modal .radio {
    width: unset !important;
}

