.wrapper {
    width: 336px;
    margin: 0 auto;
    background: #FFF;
    border-radius: 16px;
    position: relative;
    box-shadow: 0px 10px 20px rgba(34, 26, 51, 0.1);
}

.wrapper .btn {
    padding: 24px 60px !important;
}

.wrapper .btn-light-blue {
    border: 1px solid #2DCBD0;
}

.wrapper-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 16px;
    background: radial-gradient(45.98% 193.12% at 79.46% -78.75%, #9EE3ED 0%, #2DCBD0 100%);
    border-radius: 16px;
}

.wrapper-header .logo {
    display: block;
    width: 48px;
    height: 48px;
}

.wrapper-header p {
    width: 70%;
    font-weight: 700;
    color: #FFF;
}

.wrapper-header .setting {
    display: block;
    width: 16px;
    height: 16px;
}

.wrapper-body {
