    /*ползунок*/
}

.wrapper-body .wrapper-content .line {
    width: 100%;
    border: 1px solid #221A33;
    opacity: .1;
}

.wrapper-body .wrapper-content .primary-text {
    padding: 0px 40px;
    font-weight: bold;
    font-size: 30px;
    line-height: 32px;
    text-align: center;
}

.wrapper-body .wrapper-content .primary-text-1 {
    padding: 0px 20px;
