}

.wrapper-body .select-clinic {
    display: flex;
    padding-right: 9px;
    height: 430px;
    overflow-y: scroll;
}

.wrapper-body .select-clinic ul {
    width: 100%;
    margin: 0;
    padding: 0;
    list-style: none;
}

.wrapper-body .select-clinic ul li {
    margin-bottom: 10px;
    padding: 16px;
    background: #F2F5FA;
    border-radius: 16px;
}

.wrapper-body .select-clinic ul li .select-clinic-header {
    display: flex;
    flex-direction: row;
    align-self: center;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 10px;
}

.wrapper-body .select-clinic ul li .select-clinic-header span:first-child {
    flex: 3 1 70%;
    font-weight: 600;
    font-size: 15px;
    line-height: 18px;
}

.wrapper-body .select-clinic ul li .select-clinic-header span:last-child {
    display: inline-flex;
    flex: 0 3 40%;
    justify-content: center;
}

.wrapper-body .select-clinic ul li .select-clinic-body {
    padding-bottom: 4px;
}

.wrapper-body .select-clinic ul li .select-clinic-body p {
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
}

.wrapper-body .select-clinic ul li .select-clinic-footer {
    display: flex;
    flex-direction: row;
    align-self: center;
    align-items: center;
    justify-content: space-between;
}

.wrapper-body .select-clinic ul li .select-clinic-footer span:first-child {
    flex: 3 1 70%;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
}

.wrapper-body .select-clinic ul li .select-clinic-footer span:last-child {
    display: inline-flex;
    flex: 0 3 40%;
    justify-content: center;
}

.wrapper-body .select-clinic ul li:last-child {
    margin: 0;
}

.wrapper-body .select-clinic ul li:hover {
    cursor: pointer;
}

.wrapper-body .select-clinic::-webkit-scrollbar {
