.content-arrow-left,
.content-close {
    display: inline-flex;
    flex-direction: row;
    align-self: flex-end;
    width: 39px;
    height: 46px;
    padding-top: 28px;
    position: absolute;
    top: 0;
}

.content-arrow-left i,
.content-close i {
    cursor: pointer;
}

.content-arrow-left {
    justify-content: flex-start;
    left: -39px;
}

.content-close {
    justify-content: flex-end;
    right: -39px;
}

form input[type="text"],
form textarea {
    width: 100%;
    padding: 23px;
    border: none;
    outline: none;
    background: #F2F5FA;
    border-radius: 16px;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;
}

form input[type="text"] {
    text-align: center;
}

form textarea {
    height: 250px;
    resize: none;
}

form .checkbox-content {
    display: flex;
    flex-direction: row;
    align-self: center;
    padding-left: 24px;
}

form .checkbox-content label {
    font-weight: normal;
    font-size: 10px;
    line-height: 12px;
    padding-left: 22px;
}

form .checkbox-content input[type="checkbox"] {
    position: absolute;
    width: 0;
    height: 0;
}

form .checkbox-content input[type="checkbox"]:before {
    content: '';
    display: inline-block;
    position: absolute;
    width: 12px;
    height: 12px;
    margin-right: 14px;
    border: 1px solid rgba(34, 26, 51, 0.2);
    border-radius: 4px;
    transition: .2s ease-in-out;
}

form .checkbox-content input[type="checkbox"]:after {
    content: '';
    display: inline-block;
    position: absolute;
    top: 4px;
    left: 4px;
    width: 4px;
    height: 4px;
    margin-right: 14px;
    border-radius: 4px;
    transition: .2s ease-in-out;
}

form .checkbox-content input[type="checkbox"]:checked:before {
    display: inline-block;
    position: absolute;
    width: 12px;
    height: 12px;
    margin-right: 14px;
    border: 1px solid #6531A2;
    border-radius: 4px;
}

form .checkbox-content input[type="checkbox"]:checked:after {
    display: inline-block;
    position: absolute;
    width: 4px;
    height: 4px;
    border-radius: 4px;
    background: #6531A2;
}

form.form-radiobox {
    display: flex;
    flex-direction: row;
    padding-top: 20px;
}

form.form-radiobox .radiobox-content {
    display: flex;
    flex-direction: column;
    width: 50%;
    position: relative;
}

form.form-radiobox .radiobox-content ul {
    width: 100%;
    margin: 0;
    padding: 0;
    list-style: none;
}

form.form-radiobox .radiobox-content ul li {
    margin: 0;
    padding: 0 0 18px 0;
}

form.form-radiobox label {
    width: 100%;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
}

form.form-radiobox input[type="radio"] {
    position: absolute;
    width: 0;
    height: 0;
}

form.form-radiobox input[type="radio"]:before {
    content: '';
    display: inline-block;
    position: absolute;
    right: 0;
    width: 12px;
    height: 12px;
    margin-right: 14px;
    border: 1px solid rgba(34, 26, 51, 0.2);
    border-radius: 4px;
    transition: .2s ease-in-out;
}

form.form-radiobox input[type="radio"]:after {
    content: '';
    display: inline-block;
    position: absolute;
    top: 4px;
    right: 4px;
    width: 4px;
    height: 4px;
    margin-right: 14px;
    border-radius: 4px;
    transition: .2s ease-in-out;
}

form.form-radiobox input[type="radio"]:checked:before {
    display: inline-block;
    position: absolute;
    width: 12px;
    height: 12px;
    margin-right: 14px;
    border: 1px solid #6531A2;
    border-radius: 4px;
}

form.form-radiobox input[type="radio"]:checked:after {
    display: inline-block;
    position: absolute;
    width: 4px;
    height: 4px;
    border-radius: 4px;
    background: #6531A2;
}

