  width: 14px;
  height: 14px;
  margin-right: 14px;
  border: 1px solid rgba(34, 26, 51, 0.2);
  border-radius: 4px;
  transition: 0.2s ease-in-out;
}

.modal input[type=checkbox]:after {
  content: "";
  display: inline-block;
  position: absolute;
  top: 4px;
  right: 4px;
  width: 6px;
  height: 6px;
  margin-right: 14px;
  border-radius: 4px;
  transition: 0.2s ease-in-out;
}

.modal input[type=checkbox]:checked:before {
  display: inline-block;
  position: absolute;
  width: 14px;
  height: 14px;
  margin-right: 14px;
  border: 1px solid #6531A2;
  border-radius: 4px;
}

.modal input[type=checkbox]:checked:after {
  display: inline-block;
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 4px;
  background: #6531A2;
}

.modal .radio {
  width: unset !important;
}

