form .checkbox-content {
  display: flex;
  flex-direction: row;
  align-self: center;
  padding-left: 24px;
}

form .checkbox-content label {
  font-weight: normal;
  font-size: 10px;
  line-height: 12px;
  padding-left: 22px;
}

form .checkbox-content input[type=checkbox] {
  position: absolute;
  width: 0;
  height: 0;
}

form .checkbox-content input[type=checkbox]:before {
  content: "";
  display: inline-block;
  position: absolute;
  width: 12px;
  height: 12px;
  margin-right: 14px;
  border: 1px solid rgba(34, 26, 51, 0.2);
  border-radius: 4px;
  transition: 0.2s ease-in-out;
}

form .checkbox-content input[type=checkbox]:after {
  content: "";
  display: inline-block;
  position: absolute;
  top: 4px;
  left: 4px;
  width: 4px;
  height: 4px;
  margin-right: 14px;
  border-radius: 4px;
  transition: 0.2s ease-in-out;
}

form .checkbox-content input[type=checkbox]:checked:before {
  display: inline-block;
