@import './bootstrap';
@import './site-bot';
@import './components/animate';

/* .breakBtn{
    cursor: not-allowed;
} */

.bot-chat[disabled="true"]{
    cursor: not-allowed;
}

.tab__img__margin{
    margin: 30px 0;
}

.item{
    .tab__img__margin{
        img{
            margin: 0 auto;
        }
    }
}

.newtabs{
    border: 0;
    margin-top: 30px;

    justify-content: space-between;
    flex-wrap: wrap;
}

@media (max-width: 540px){
    .newtabs{
        justify-content: flex-start;
        row-gap: 20px;
        column-gap: 20px;
    }
}

.modal{

    .modal__close{
        cursor: pointer;
        position: absolute;
        top: -25px;
        right: -15px;
        width: 18px;
        height: 18px;
        z-index: 9999;
    
        &::after{
            position: absolute;
            content: '';
            width: 100%;
            height: 2px;
            background-color: #fff;
            transform: rotate(45deg);
        }
    
        &::before{
            position: absolute;
            content: '';
            width: 100%;
            height: 2px;
            background-color: #fff;
            transform: rotate(-45deg);
        }
    }
    

}

a.policy{
    cursor: pointer;
}

#ZvonokModal, #Connect, #Policy{
    /* display: flex!important; */
    background-color: rgba(34, 26, 51, 0.9)!important;

    .modal__dialog{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        .modal-content, .modal-body{
            width: 630px;
            min-height: 300px;
            height: 100%;
            background-color: transparent;
            border: 0;
        }

        .modal-body{
            background: radial-gradient(70.1% 110.5% at 56.22% -42.69%, #A160ED 0%, #6531A2 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */, #FFFFFF;
            border-radius: 16px;
            padding: 0;
        }

    }

}

.form__callback{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px;

    .radio-group{
        margin-top: 20px;

        label, a{
            color: #fff;
            font-family: 'MontserratRegular';
            font-size: 12px;
        }
    }

    .col-12{
        margin-top: 20px;
        padding-bottom: 40px;
        display: flex;
        flex-direction: column;
        align-items: center;

        .form-control{
            outline: none;
            border: none;
            background: rgba(242, 245, 250, 0.1);
            border-radius: 16px;
            width: 330px;
            height: 64px;
            color: white;
            font-size: 15px;
            font-family: 'MontserratRegular';

            &:focus{
                border: 0;
                outline: 0;
            }

            &::placeholder{
                color: white;
            }
        }
    }

    .form__footer{
        padding-top: 40px;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;

        .radio-group{
            input[type=checkbox]:before {
                content: "";
                display: inline-block;
                position: absolute;
                right: 0;
                width: 14px;
                height: 14px;
                margin-right: 14px;
                border: 1.5px solid #fff!important;
                border-radius: 4px;
                -webkit-transition: 0.2s ease-in-out;
                -o-transition: 0.2s ease-in-out;
                transition: 0.2s ease-in-out;
            }

            input[type=checkbox]:after{
                background: transparent!important;
            }

            input[type=checkbox]:checked:after {
                background: #fff!important;
            }
        }

        &::after{
            position: absolute;
            content: '';
            top: 0;
            left: 0;
            width: 100%;
            height: 1px;
            background-color: #fff;
            opacity: 0.1;
        }
    }
}


#Policy{

    .modal__dialog{
        position: absolute;
        top: 50px;
        left: 50%;
        transform: translate(-50%, 0)!important;

        .modal-content, .modal-body{
            width: 1000px!important;
        }
    }

    .modal-body{
        display: flex;
        flex-direction: column;
        padding: 20px!important;

        p.header__politcy{
            text-align: center;
            color: #fff;
            font-family: 'MontserratBold';
            font-size: 18px;
        }

        p.title__policy{
            color: #fff;
            margin-top: 20px;
            font-size: 16px;
        }

        p.sub-title__policy{
            margin: 15px 0;
            font-size: 14px;
            color: #fff;
        }
        
        p.text__policy, p.list__policy{
            font-size: 14px;
            color: #fff;
            margin-top: 5px;
        }

        p.list__policy{
            margin-left: 10px;
        }
    }

}

@media (max-width: 1050px){
    #Policy{

        .modal__dialog{
            position: absolute;
            top: 0;
            left: 0;
            transform: translate(0, 0)!important;
    
            .modal-content, .modal-body{
                width: 100vw!important;
            }
        }

        .modal-body{
            border-radius: 0!important;
        }

    }

    .modal{
        .modal__close{
            top: 25px;
            right: 15px;
            position: fixed!important;
        }
    }
}

@media (max-width: 480px){
    #Policy{

        .modal__dialog{

            .modal-content, .modal-body{
                width: 100%!important;
            }
        }

    }
}

@media (max-width: 642px){
    #ZvonokModal, #Connect{
        .modal__dialog{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
    
            .modal-content, .modal-body{
                width: 100vw;
                min-height: 300px;
                background-color: transparent;
                border: 0;
            }
        }
    }

    .form__callback{

        .form__footer{
            padding-left: 10px;
            padding-right: 10px;
            button{
                width: 100%;
            }
        }
    }
}

@media (max-width: 480px){
    #ZvonokModal, #Connect{
        width: 100%;
        .modal__dialog{
            width: 100%;
            position: absolute;
            top:0;
            left:0;
            transform: translate(0, 0);
            border: 0;
            border-radius: 0;
    
            .modal-content, .modal-body{
                width: 100%;
                height: 100vh;
                background-color: transparent;
                border: 0;
                border-radius: 0;
            }
            
        }
    }
}

@media (max-width: 420px){
    .form__callback{
        margin-top: 40px;
    }
}



/* ======================================= */

.choice__city__question{
    position: absolute;
    width: 300px;
    background: radial-gradient(70.1% 110.5% at 56.22% -42.69%, #A160ED 0%, #6531A2 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */, #FFFFFF;
    display: none;
    border-radius: 16px;
    padding: 20px;
    flex-direction: column;
    margin-top: 40px;

    p.choice__city__question__name{
        font-family: 'MontserratRegular';
        color: white;
        font-size: 18px;
        text-align: center;

        span{
            font-family: 'MontserratBold';
        }
    }

    .choice__city__question__btn__wrapp{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        margin-top: 10px;
    }

    a.choice__city__question__btn{
        padding: 10px;
        /* background-color: #fff; */
        color: white;
        cursor: pointer;
    }

    button.choice__city__question__btn{
        outline: 0;
        border: 0;
        background-color: transparent;
        color: white;
        margin-top: 10px;
    }

    input#choice__city{
        outline: none;
        border: none;
        background: rgba(242, 245, 250, 0.1);
        border-radius: 16px;
        color: white;
        font-size: 15px;
        font-family: 'MontserratRegular';
        height: 40px;
        padding: 0 20px;

        &::placeholder{
            color: white;
            font-size: 15px;
            font-family: 'MontserratRegular';
        }
    }

    .choice__city__input__wrapp{
        display: none;
        flex-direction: column;
        align-items: center;
        margin-top: 15px;
    }
}

@media (max-width: 990px){
    .choice__city__question{
        position: static;
    }

    .choise-city{
        flex-direction: column;
        align-items: center;
    }
}

@media (max-width: 460px){
    .choice__city__question{
        position: absolute;
        width: 100vw;
        left: 0;
        border-radius: 0;
        background: transparent;
        margin-top: 150px;

        .choice__city__question__btn__wrapp{
            justify-content: center;

            .btn__disagree{
                margin-left: 30px;
            }
        }

        input#choice__city{
            background: radial-gradient(70.1% 110.5% at 56.22% -42.69%, #A160ED 0%, #6531A2 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */, #FFFFFF;
        }
    }

    .choise-city{
        flex-direction: column;
        align-items: center;
        margin-bottom: 50px;
        .choise-city-text{
            display: none;
        }
    }
}


/* ======================================= */

.fadeUp__start.anim__item{
    transform: translate(0, 100px);
    opacity: 0;
    transition: .5s ease;

    &.delay3ms{
        transition-delay: .3s;
    }

    &.delay8ms{
        transition-delay: .8s;
    }

    &.delay1s{
        transition-delay: 1s;
    }

    &.anim__active{
        transform: translate(0, 0);
        opacity: 1;
    }
}

.fadeIn__start.anim__item{
    transform: scale(0.5);
    opacity: 0;
    transition: .5s ease;

    &.delay3ms{
        transition-delay: .3s;
    }

    &.delay5ms{
        transition-delay: .5s;
    }

    &.delay8ms{
        transition-delay: .8s;
    }

    &.delay1s{
        transition-delay: 1s;
    }

    &.anim__active{
        transform: scale(1);
        opacity: 1;
    }
}

.fade__start.anim__item{
    opacity: 0;
    transition: .5s ease;

    &.delay3ms{
        transition-delay: .3s;
    }

    &.delay5ms{
        transition-delay: .5s;
    }

    &.delay8ms{
        transition-delay: .8s;
    }

    &.delay1s{
        transition-delay: 1s;
    }

    &.anim__active{

        opacity: 1;
    }
}

@media (max-width: 990px){
    .footer__column__style{
        flex-direction: column;
    }
}